import React from 'react';

const Home = () => {
    return (
        <div>
            <h1>Qu es Gestin integral de riesgos de desastres?</h1>
            <h2>La ley de gestin de riesgos de desastres contempla
                la posibilidad de ocurrencia de un evento que pueda afectar
                economia, bienes materiales o vidas humanas</h2>
            <h2>La Gestin del Riesgos se hace referencia
                al proceso social de planeacin, ejecucin, seguimiento y
                evaluacin de polticas y acciones para el conocimiento
                del riesgo y promocin de una mayor conciencia del mismo,
                con el nimo de impedir o evitar que se genere, de
                reducirlo o controlarlo cuando ya existe y para preparar-
                se y manejar las situaciones de desastre, as como para la
                posterior recuperacin.</h2>
            <h1>Qu es Agenda de reduccin de riesgos de desastres?</h1>
            <h2>Es el conjunto de normas estrategias, mapas y actividades que
                realiza una comunidad para evitar ser afectada y prevenir
                cualquier afectacin a nivel de desastre </h2>

            <h1>Quen es el responsable de la gestin de riesgos?</h1>
            <h2>Dentro de la ley Organica de Gestin integral de
                riesgos de desastres cada persona dentro del territorio
                nacional es responsable de gestin de riesgos </h2>
        </div>
    );
};

export default Home;